<template>
	<div class="item">
		<div class="meal" :key="item.id" v-for="item in serverMeal" :style="{ backgroundImage: 'url('+ item.headImg +')' }" >
			<div class="hospital">
				<div class="logo">
					<img :src="item.headLogo">
				</div>
				<div class="title">{{item.hospital}}</div>
				<div class="level">{{item.level}}</div>
			</div>

			<Setmeal :key="meal.id" v-for="meal in item.meal" :data="meal" @changeMeal="changeMeal" />
		</div>
	</div>
</template>

<script>
	import Setmeal from '@/components/Setmeal.vue'
	import Contact from '@/components/Contact.vue'

	export default {
		name: 'meal',
		components: { Setmeal },
		data() {
			return {
				serverMeal: []
			}
		},
		created() {
			fnServerMeal(this);
			this.$emit('handlerTabbar', true);
		},
		methods: {
			changeMeal(item) {
				this.$emit('handlerTabbar', false);
				this.$router.push({ path: 'meal-detail', query: { id: item.id }});
			}
		}
	}

	function fnServerMeal(that, params) {
		that.$request.serverMeal(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.serverMeal = result.list;
			}
		})
	}
</script>

<style lang="scss">
	@import './../styles/meal.scss'
</style>